liveContent{
    display: flex;
    flex-direction: column;
    gap: 40px;
    overflow: hidden;
}

.Liveshowh1 {
    display: flex;
    justify-content: space-between;
}



.Liveshowh1 h1 {
    font-family: 'Gilroy-Bold';
    font-size: 32px;
    line-height: 40px;
    color: #FFFFFF;
}

.SeeAll {
    cursor: pointer;
    max-width: 74px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
}


.SeeAll h3 {
    cursor: inherit;
    font-family: 'Gilroy-regular';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}

.SeeAllSvg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.LiveShowSlide {
    overflow: auto;
    width: 500%;
    max-height: 180px;
}

.LiveShowSlide h1 {
    font-family: 'Gilroy-medium';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;

    color: #FFFFFF;
}

.LiveShowSlide h2 {
    font-family: 'Gilroy-regular';
    font-size: 14px;
    line-height: 20px;

    color: #807E81;
}



.LiveShowSlide li {
    width: 100%;
    list-style: none;
    display: flex;
    flex-direction: row;
}

.LiveshowImgContainer {
    width: 126px;
    height: 100px;
}

.LiveshowImgContainer img {
    width: 126px;
    height: 100px;
    object-fit: cover;
    border-radius: 2px;
}


.LiveShowContent ul {
    overflow: auto;
    width: 500%;
    height: 100%;

    display: flex;
    flex-direction: row;

    gap: 24px;
}

.LiveShowSlideContent {
    max-width: 400px;
    height: 100px;
    display: flex;
    flex-direction: row;
}

.LiveShowInfo {
    padding-left: 16px;
    max-width: 280px;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
}

.LiveShowStatus {
    width: 73px;
    height: 26px;

    border: 0.7px solid transparent;

    border-radius: 2px;

    background:
        linear-gradient(to right, #0A070B, #0A070B),
        linear-gradient(to right, #F41B3B, #1884F7);
    background-clip: padding-box, border-box;
    background-origin: padding-box, border-box;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px 8px;
    gap: 10px;
}

.LiveShowStatus p {
    width: 57px;
    height: 18px;

    font-family: 'Gilroy-Medium';
    font-size: 14px;
    line-height: 18px;

    color: #F41B3B;
    flex: none;
    order: 0;
    flex-grow: 0;

}

@media screen and (max-width: 721px) {
    
    .LiveShowSlideContent {
        gap: 8px;
        max-width: 160px;
        height: 180px;
    }

    .LiveShowSlide li{
        flex-direction: column;
    }

    .LiveshowImgContainer {
        width: 160px;
        height: 100px;
    }

    .LiveshowImgContainer img {
        width: 160px;
        height: 100px;
    }

    .LiveShowInfo {
        width: 142px;
        height: 72px;
        gap: 0px;

        padding-left: 0px;
    }

    .LiveShowInfo h1 {
        width: 141px;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px;
    }
    .LiveShowInfo h2 {
        font-size: 12px;
        line-height: 20px;
    }



    .LiveShowStatus{
        width: 65px;
        height: 24px;
    }
}